body{
  margin: 0px;
}

.maindiv{
  background-color: rgb(5, 204, 204);
  height: 100vh;
  display: grid;
  justify-content: center;
  align-items: center;
}

.div{
  width: 40vh;
  height: 65vh;
  background-color:floralwhite;
  border: solid 2px black;
  border-radius: 20px;
  box-shadow: 10px 10px 20px black;
}
h1{
  margin-top: 3vh;
  text-align: center;
  background-color: burlywood;
}
.input{
  margin-top: 5vh;
  display: flex;
  justify-content: center;

}
button{
  width: 6vh;
  height: 6vh;
  background-color: mediumorchid;
  border-radius: 100%;
  color: azure;
  margin-left: 3vh;
  border: none;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  padding: 0px 7px 5px 7px;
  box-shadow: 2px 3px 5px black;
}
button:hover{
  background-color:green;
}
input{
  margin-left: 3vh;
  width: 20vh;
  border: none;
  border-bottom: solid 3px burlywood;
  outline: none;
  background-color: floralwhite;
  font-size: 1.3em;
}

.delete{
  width: 3vh;
  height: 3vh;
  display: grid;
  justify-content: center;
  display: inline-block;
  margin: 2vh 3vh;
  
}
.del{
  margin-left: -1vh;
  background-color:moccasin;
  border-radius: 100%;
  color: mediumorchid;
}


.del:hover{
  color: red;
}
.adds{
  width: 5vh;
  height: 5vh;
  display: grid;
  justify-content: center;
}
.add{
  background-color:lightgoldenrodyellow;
  border-radius: 100%;
  color: mediumorchid;
  
}
.add:hover{
  color: green;
  
}
.listitem{
  width: auto;
  height: auto;
  display: inline-block;
}

